@import url(https://fonts.googleapis.com/css?family=Heebo);
body {
  margin: 0;
  padding: 0;
  font-family: Heebo, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
  --seashell: seashell;
  --black: #363636;
  --contained-width: 40em;
  --contained-max-width: 95vw;
  --nav-height: 6em;
}

#app {
  min-height: 100vh;
  overflow-x: hidden;
  background: rgb(217,16,35);
  background: #D91023;
  background: var(--peru-red);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

#nav-bar-top {
  width: 100%;
  z-index: 9999;
}

#nav-bar-container {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 6em;
  height: var(--nav-height);
  background: seashell;
  background: var(--seashell);
  box-shadow: 0px 2px 4px 0px rgb(0, 0, 0, 0.30);
}

#content-container {
  margin-top: 6em;
}
:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
}

.word-card {
	background-color: white;
	margin: 1em 1em 1em 1em;
	padding: 2em 3em;
	border-radius: 1em;
	border: 2px solid #FCD116;
	border: 2px solid var(--peru-yellow) ;
	width: 40em;
  position: relative;
	max-width: var(--contained-max-width);
	-webkit-animation: word-card-in 1s ease-out;
	        animation: word-card-in 1s ease-out;
	box-shadow: 0px 1px 2px 0px rgb(0, 0, 0, 0.30);
}

.word-card * {
  font-family: 'Heebo';
}

.content h2 {
	color: #007EA8;
	color: var(--peru-blue);
}

@-webkit-keyframes word-card-in {
  0% {
    opacity: 0;
    top: 2em;
  }

  100% {
    top: 0em;
    opacity: 1;
    margin: 1em 1em 1em 1em;
  }
}

@keyframes word-card-in {
  0% {
    opacity: 0;
    top: 2em;
  }

  100% {
    top: 0em;
    opacity: 1;
    margin: 1em 1em 1em 1em;
  }
}


#word-search-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

#search-bar-outer {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: var(--seashell);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px white solid;
  padding: 2em;
  width: 100vw;
  color: var(--black);
}

#search-bar {
  width: 100%;
  max-width: var(--contained-width);
}

#search-bar.ui.input {
  color: var(--peru-blue)
}

#search-input {
  border-radius: 3em;
  box-shadow: 0px 1px 2px 0px rgb(0, 0, 0, 0.30);
}

#results-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.results-list {
  -webkit-animation: results-list-in 1s;
          animation: results-list-in 1s;
}

@-webkit-keyframes results-list-in {
  from {
    padding-top: 3em;
  }

  to {
    padding-top: 0;
  }
}

@keyframes results-list-in {
  from {
    padding-top: 3em;
  }

  to {
    padding-top: 0;
  }
}
#nav-outer {
	height: var(--nav-height);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}

#nav-top {
	width: var(--contained-width);
	max-width: var(--contained-max-width);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
}

#nav-top img {
	max-height: 100%;
	width: 6em;
	object-fit: contain;
}

#nav-top h1 {
	margin: auto 0 auto 1em;
}

#nav-links-outer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	max-width: var(--contained-max-width);
	width: 100%;
}

#nav-links {
	max-width: var(--contained-max-width);
	width: var(--contained-width);
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 0;
	overflow-x: scroll;
	overflow-y: hidden;
	border: 0px;
	background-color: transparent;
	box-shadow: 0 0 0 0 transparent;
}

#nav-links::-webkit-scrollbar {
	height: 2px;
}

.nav-icon {

}

.nav-icon::before {
	color: var(--black);
	display: none;
}
